import React, {Component} from 'react'
import {Button,
	Container,
	Row,
	Col,
	FormGroup,
	Label,
	Form,
	Input
} from 'reactstrap'

const Web3 = require('web3')
const web3 = new Web3(Web3.givenProvider || 'https://etherscan.io')
import Navbar from './navbar.jsx'

export default class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			address : ''
		}
	}

	render() {
		return (
			<Container style={{'marginTop' : '40px'}}>
				<Col md="12">
					<Navbar/>
				</Col>
				<Col md="3"></Col>
				<Col md="6" className="box">
					<center>
						<h1>Create New Wallet</h1>
						<FormGroup>
							<Label for="etherKey">Enter Password</Label>
							<Input type="text" name="etherKey" id="etherKey" placeholder="Do NOT forget to save this!"/>
						</FormGroup>

						<Button onClick={() => this._createAccount()} color="primary">CREATE WALLET</Button>

						<br/>
						<br/>

						{this._renderAddress()}
					</center>
				</Col>
			</Container>
		)
	}

	_createAccount() {
		let account = web3.eth.accounts.create()
		this.setState({
			address : account.address
		})
	}

	_renderAddress() {
		if (this.state.address != '') {
			return (
				<p className="alert alert-success">Your Ethereum Wallet <b>{this.state.address}</b></p>				
			)
		}
	}
}


